.contact-section {
  background: #f7f8fa;
  position: relative;
  padding-top: 60px;
  padding-bottom: 0;
  min-height: 950px; // !!! головне, щоб все вміщалось

  .container {
    padding-top: 0;
    padding-bottom: 0;
  }

  .contact-subtitle {
    color: #008de6;
    font-weight: 700;
    font-size: 15px;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-bottom: 8px;
  }
  .contact-title {
    font-size: 36px;
    font-weight: 700;
    color: #222;
    margin-bottom: 0;
  }
  .blue-line img {
    display: block;
    margin: 18px auto 0 auto;
    height: 4px;
  }

.contact-info-row {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: 180px; // велика відстань між блоками
  margin: 60px 0 0 0;
  margin-bottom: 300px;

  @media (max-width: 1200px) {
    gap: 100px;
  }
  @media (max-width: 991.98px) {
    gap: 60px;
    margin-bottom: 70px;
  }
  @media (max-width: 767.98px) {
    flex-direction: column;
    gap: 28px;
    align-items: center;
    margin-bottom: 30px;
  }
}
.contact-info-block {
  flex: 1 1 0;
  max-width: 300px;
  min-width: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  text-decoration: none;
  outline: none;
  background: transparent;
  transition: transform 0.2s, box-shadow 0.2s;
  cursor: pointer;

  &:hover,
  &:focus {
    .contact-icon-img {
      filter: none;
      opacity: 0.8;
    }
    .contact-info-text {
      color: #008de6;
    }
  }

  .contact-icon-img {
    width: 32px;
    height: 32px;
    margin-bottom: 14px;
    object-fit: contain;
    filter: grayscale(0) brightness(1.1);
    opacity: 0.95;
    transition: filter 0.2s, opacity 0.2s;
    display: block;
  }
  .contact-info-text {
    color: #888;
    font-size: 15px;
    line-height: 1.5;
    text-align: center;
    transition: color 0.2s;
    user-select: none;
    font-weight: 400;
    font-family: 'Source Sans Pro', Arial, sans-serif;
    word-break: break-word;
  }
}
  }

  .contact-map {
    width: 100vw;
    margin-left: 50%;
    transform: translateX(-50%);
    position: relative;
    z-index: 1;
    margin-bottom: 0;
    iframe {
      width: 100%;
      height: 400px;
      border: none;
      display: block;
      @media (max-width: 991.98px) {
        height: 300px;
      }
      @media (max-width: 767.98px) {
        height: 200px;
      }
    }
  }

  .contact-form-wrapper {
    position: absolute;
    left: 50%;
    bottom: 320px; // форма накладається тільки на мапу!
    transform: translateX(-50%);
    z-index: 2;
    width: 100%;
    display: flex;
    justify-content: center;
    pointer-events: auto;
    margin-top: 0;

    @media (max-width: 991.98px) {
      bottom: 220px;
      padding: 0 10px;
    }
    @media (max-width: 767.98px) {
      position: static;
      transform: none;
      margin: -70px auto 0 auto;
      width: 100%;
      justify-content: center;
      padding: 0 5px;
    }
  }

  .contact-form {
    background: #18181e;
    border-radius: 4px;
    padding: 36px 38px 28px 38px;
    box-shadow: 0 8px 32px rgba(0,0,0,0.13);
    width: 100%;
    max-width: 760px;
    display: flex;
    flex-direction: column;
    gap: 22px;
    @media (max-width: 991.98px) {
      padding: 18px 10px 14px 10px;
      max-width: 98vw;
    }
  }

  .form-grid {
    display: flex;
    gap: 24px;
    @media (max-width: 767.98px) {
      flex-direction: column;
      gap: 10px;
    }
    .form-left {
      flex: 1 1 260px;
      display: flex;
      flex-direction: column;
      gap: 16px;
    }
    .form-right {
      flex: 1 1 340px;
      display: flex;
      flex-direction: column;
      textarea {
        height: 100%;
        min-height: 120px;
        resize: vertical;
      }
    }
  }

  .form-control {
    background: transparent;
    border: 1px solid #333;
    border-radius: 2px;
    color: #fff;
    font-size: 15px;
    margin-bottom: 0;
    padding: 12px 16px;
    transition: border-color 0.2s;
    &:focus {
      border-color: #00aaff;
      background: #222;
      color: #fff;
    }
    &::placeholder {
      color: #888;
    }
  }

  .form-actions {
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
  }
  .send-btn {
    background: #00aaff;
    border: none;
    color: #fff;
    font-weight: 700;
    border-radius: 2px;
    padding: 12px 32px;
    font-size: 16px;
    text-transform: uppercase;
    transition: background 0.2s;
    &:hover, &:focus {
      background: #0070b8;
    }
  }
  .thankyou-modal {
  display: none;
  position: fixed;
  z-index: 10000;
  left: 0; top: 0; right: 0; bottom: 0;
  background: rgba(0,0,0,0.45);
  justify-content: center;
  align-items: center;
  transition: opacity 0.2s;

  &.active {
    display: flex;
  }

  .thankyou-modal-content {
    background: #fff;
    border-radius: 8px;
    padding: 36px 38px 28px 38px;
    box-shadow: 0 8px 32px rgba(0,0,0,0.18);
    text-align: center;
    min-width: 320px;
    max-width: 90vw;
  }
  .thankyou-title {
    font-size: 28px;
    font-weight: 700;
    color: #008de6;
    margin-bottom: 12px;
  }
  .thankyou-text {
    color: #333;
    font-size: 17px;
    margin-bottom: 26px;
    line-height: 1.6;
  }
  .thankyou-close {
    background: #008de6;
    color: #fff;
    border: none;
    border-radius: 3px;
    font-size: 15px;
    font-weight: 600;
    padding: 12px 32px;
    cursor: pointer;
    transition: background 0.2s;
    &:hover, &:focus {
      background: #005fa3;
    }
  }
}


